body {
  margin: 0 auto;
  padding: 0;
  font-family: cursive;
}
.App {
  height: auto;
  width: 100wh;
}

/* NAVBAR SECTION */
.navbar {
  position: fixed;
  margin: 0 auto;
  top: 0;
  width: 100%;
  height: auto;
  z-index: 100;
  background-color: #ffffff;
  border-bottom: 1px solid #cccccc;
  padding-bottom: 20px;
  padding-top: 20px;
}

.navbar-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style: none;
  margin: 0;
  padding: 0 45rem;
}

.nav-item {
  margin: 0;
}

.nav-link {
  color: #333333;
  text-decoration: none;
  font-size: 18px;
  font-weight: bold;
  padding: 5px 10px;
  border-radius: 5px;
  transition: all 0.3s ease;
}

.nav-link:hover {
  cursor: pointer;
  background-color: #f5f5f5;
}

@media screen and (max-width: 768px) {
  .navbar-nav {
    flex-direction: row;
    align-items: flex-start;
    padding: 0;
  }

  .nav-link {
    display: block;
    padding: 10px;
    font-size: 16px;
  }
}

/* HOME SECTION*/
.slider-container {
  margin: 0 auto;
  width: fit-content;
  position: relative;
}

.gif-container {
  margin: 0 auto;
  align-items: center;
  height: 400px;
  width: 1200px;
  padding-bottom: 50px;
  background-image: url(./assets//try.webp);
  background-repeat: no-repeat;
  background-size: 1200px 500px;
  animation: pulse 30s infinite ease-in-out;
  filter: brightness(50%);
}

.gif {
  width: 100%;
  height: 100%;
}
@keyframes pulse {
  0% {
    background-image: url(./assets//try.webp);
  }

  100% {
    background-image: url(./assets//try2.webp);
  }
}

.slider-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 200px;
}

.slider-item {
  display: none;
  width: 100%;
  text-align: center;
}

.slider-item.active {
  display: block;
}

.typewriter {
  display: inline-block;
  animation: typing 2s steps(20, end), blink-caret 1s step-end infinite;
  white-space: nowrap;
  overflow: hidden;
  font-family: cursive;
  font-size: 40px;
  border-right: 0.05em solid black;
}

@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

@keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: black;
  }
}

.img-cont {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  height: 300px;
  width: 300px;
}

.img-cont img:nth-child(odd) {
  height: 100%;
  width: 100%;
  border-radius: 50%;
  margin-right: 40px;
  margin-left: 50px;
  object-fit: cover;
  border: 2px solid #ccc;
}

.img-cont img:nth-child(even) {
  opacity: 0.6;
  height: 30%;
  width: 30%;
  margin-right: 50px;
  margin-left: 50px;
  object-fit: cover;
}

.name-cont {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
}
.name-cont text {
  font-size: 20px;
  margin-top: 20px;
  margin-right: 30px;
  margin-left: 80px;
}

.Story {
  margin-top: 300px;
  height: auto;
}

.content h1 {
  font-family: cursive;
  text-align: center;
  font-size: 45px;
  margin-top: 1rem;
}
.content h2 {
  text-align: center;
}


/* STORY SECTION */
.timeline {
  background-color: white;
  position: relative;
  max-width: 800px;
  margin: 0 auto;
}

.timeline:before {
  content: "";
  position: absolute;
  left: 50%;
  top: 0;
  transform: translateX(-50%);
  width: 2px;
  height: 100%;
  background: #ccc;
}

.timeline-item {
  position: relative;
  padding: 50px 20px;
  margin: 50px 0;
}

.timeline-item:nth-child(even) .timeline-year {
  position: absolute;
  right: 250px;
  top: 50%;
  transform: translateY(-50%);
  width: 80px;
  text-align: left;
  font-size: 30px;
  font-weight: bold;
  color: #444;
}

.timeline-item:nth-child(even) .timeline-content {
  margin-right: 450px;
  text-align: right;
}

.timeline-item:nth-child(odd) .timeline-year {
  position: absolute;
  left: 250px;
  top: 50%;
  transform: translateY(-50%);
  width: 80px;
  text-align: right;
  font-size: 30px;
  font-weight: bold;
  color: #444;
}

.timeline-item:nth-child(odd) .timeline-content {
  margin-left: 450px;
}

.timeline-content h3 {
  margin-top: 0;
}

.timeline-content p {
  margin-bottom: 0;
  text-align: justify;
  text-justify: inter-word;
}
.bullet {
  background-color: #444;
  border-radius: 50%;
  height: 15px;
  width: 15px;
  margin: 0 auto;
}

/* GALLERY SECTION */
.gallery {
  height: auto;
}

.gallery-cont h1 {
  font-family: cursive;
  text-align: center;
  font-size: 45px;
  margin-top: 1rem;
}
.gallery-list {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  height: 300px;
  width: 300px;
}

.gallery-list img {
  height: 80%;
  width: 80%;
  margin-right: 20px;
  margin-left: 20px;
  object-fit: contain;
}
.gallery-list img:hover {
  transition: transform 0.5s;
  transform: scale(1.2);
  cursor: pointer;
}

/* FADE EFFECT */
.fade-in {
  opacity: 0;
  transition: opacity 1s ease-in-out;
}
.fade-in.active {
  opacity: 1;
}

/* LETTER SECTION */
.message {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 200px;
}

.button {
  padding: 12px 24px;
  border: none;
  border-radius: 4px;
  font-size: 16px;
  color: #fff;
  background-color: #4b4a4a;
  cursor: pointer;
}
.button:hover {
  background-color: rgb(187, 47, 47);
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999;
  opacity: 0;
  transition: opacity 5s ease-in-out;
}

.overlay.show {
  opacity: 1;
}

.image {
  max-width: 80%;
  max-height: 80%;
  border-radius: 10px;
}

.button-x {
  position: absolute;
  top: 0;
  right: 0;
  background-color: transparent;
  color: white;
  border: none;
  padding: 10px;
  font-size: 25px;
  cursor: pointer;
}
.footer{
  margin: 0 auto;
  text-align: center;
  bottom: 0;
  width: 100%;
  height: auto;
  border-top: 1px solid #cccccc;
}
